export const popPresent = ()=> {
    const popPresent = document.querySelector('.present__thanks')
    const popContent = document.querySelector('.present__content')
    const presentForm = document.querySelector('.present form')

    presentForm?.addEventListener('wpcf7mailsent',()=> {
        popContent.style= 'opacity:0;pointer-events: none;';
        popPresent.style = 'opacity: 1;pointer-events: visible;'
    })
}