export const reviewsSlider = ()=> {

    const swiperReviews = new Swiper('.reviews__swiper', {
        autoHeight: true,
        spaceBetween: 15,
        observer: true,
        observeParents: true,
        navigation: {
            nextEl: '.reviews__swiper-next',
            prevEl: '.reviews__swiper-prev',
        },
        breakpoints: {
            768: {
                slidesPerView: 2,
                spaceBetween: 30,
                autoHeight: false,
            },
            1024: {
                slidesPerView: 2,
                spaceBetween: 30,
                autoHeight: false,
                navigation: {
                    nextEl: '.reviews__swiper-desk-next',
                    prevEl: '.reviews__swiper-desk-prev',
                }
            },
            1280: {
                autoHeight: false,
                slidesPerView: 3,
                spaceBetween: 30,
                navigation: {
                    nextEl: '.reviews__swiper-desk-next',
                    prevEl: '.reviews__swiper-desk-prev',
                }
            }
        }
    });

}