import {variables} from "../../vars.js";

export const popTariffs = ()=> {
    const btnPop = document.querySelectorAll('.tariffs__btn')
    const popTariffs = document.querySelector('.tariffs__pop')
    const popItems = document.querySelectorAll('.tariffs__pop-item')
    const btnPopClose = document.querySelectorAll('.tariffs__pop-close')
    const popThanks = document.querySelector('.tariffs__pop-thanks')

    btnPop?.forEach((item,index)=> {
        item.addEventListener('click',(e)=> {
            e.preventDefault()
            popTariffs?.classList.add('active')
            popItems[index].classList.add('active')
            variables.bodySite.classList.add('overflow-hidden')
            if (popThanks.classList.contains('active')) {
                popThanks.style = 'pointer-events: visible';
            }
        })
    })

    popTariffs?.addEventListener('click',(e)=> {
        if (e.target === popTariffs.querySelector('.container') || e.target === popTariffs) {
            popTariffs?.classList.remove('active')
            variables.bodySite.classList.remove('overflow-hidden')
            popThanks.style = 'pointer-events: none';
            popItems.forEach((item)=> {
                setTimeout(()=> {
                    item.classList.remove('active')
                },500)
            })
        }
    })

    btnPopClose?.forEach((item,index)=> {
        item.addEventListener('click',(e)=> {
            popTariffs?.classList.remove('active')
            variables.bodySite.classList.remove('overflow-hidden')
            popThanks.style = 'pointer-events: none';
            setTimeout(()=> {
                popItems[index].classList.remove('active')
            },500)
        })
    })

    popTariffs?.addEventListener('click',()=>{
        popThanks?.classList.add('active');
        popItems?.forEach((item)=> {
            item.classList.add('hide')
        })
    })
}