export const popDoubts = ()=> {
    const popDoubts = document.querySelector('.doubts__thanks')
    const popContent = document.querySelector('.doubts__content')
    const doubtsForm = document.querySelector('.doubts form')

    doubtsForm?.addEventListener('wpcf7mailsent',()=> {
        popContent.style= 'opacity:0;pointer-events: none;';
        popDoubts.style = 'opacity: 1;pointer-events: visible;'
    })
}